export function translationProvider(language) {
    return function (key) {
        if (key === undefined) {
            return undefined;
        }
        if (!(key in dictionary)) {
            return "err: key ".concat(key, " does not exist.");
        }
        else if (!(language in dictionary[key])) {
            return "err: language ".concat(language, " does not exist for key ").concat(key, ".");
        }
        else {
            return dictionary[key][language];
        }
    };
}
